import React, { useRef } from "react";
import style from "./CreateMaster.module.css";
import { Dialog } from "@mui/material";
import { useTranslation } from "react-i18next";
import "../AddDisplay.css";
import { useState } from "react";
import {
  useOneDepartmentAddClassMaster,
  useOneDepartmentAddSubjectMaster,
  useOneDepartmentAllClassMaster,
  useOneDepartmentAllSubjectMaster,
} from "../../../../Department/Institute/Components/DepartmentApi/depart-api";
import LoaderButton from "../../../../Loader/LoaderButton";
import StringLength from "../../../../Custom/Validation/StringLength";

function Options({ options, switchOption, customStyle }) {
  return (
    <div className={style.profileToolTip} style={{ ...customStyle }}>
      <div className={style.triangle}></div>

      <div className={style.profilelist}>
        {options.map((option, index) => (
          <div value={option} onClick={() => switchOption(option)} key={index}>
            {option}
          </div>
        ))}
      </div>
    </div>
  );
}

function CreateMaster({ departmentModal, hideModal, id, did }) {
  const { t } = useTranslation();
  const [click, setClick] = useState(false);
  const [clickSubject, setClickSubject] = useState(false);
  const inputRef = useRef();
  const [master, setMaster] = useState("Standard/Grade/Level Master");
  const selectOptions = ["Standard/Grade/Level Master", "Subject Master"];
  const selectOptionSubject = [t("mandatory"), t("optional_")];
  const [disabled, setDisabled] = useState(false);
  const [fillAllDetail, setFillAllDetail] = useState(true);
  const [subjectType, setSubjectType] = useState("Mandatory");
  const [subjectName, setSubjectName] = useState("");
  const switchMaster = (option) => {
    setMaster(option);
  };
  const switchSubjectType = (option) => {
    setSubjectType(option);
  };

  //for the subject Create
  const [addSubjectMaster] = useOneDepartmentAddSubjectMaster();
  const { allSubjectMasterRefetch } = useOneDepartmentAllSubjectMaster({
    data: {
      id: id,
      did: did,
    },
    skip: !id,
  });

  //for the master create
  const [addClassMaster] = useOneDepartmentAddClassMaster();
  const { allClassMasterRefetch } = useOneDepartmentAllClassMaster({
    data: {
      id: id,
      did: did,
    },
    skip: !id,
  });

  const masterSubmitHandler = () => {
    if (master === "Standard/Grade/Level Master") {
      if (inputRef.current.value) {
        setDisabled(true);
        addClassMaster({
          className: { className: inputRef.current.value },
          id: id,
          did: did,
        })
          .then((res) => {
            if (res?.data) {
              hideModal(false);
              setDisabled(false);
              inputRef.current.value = "";
              allClassMasterRefetch();
            }
          })
          .catch({});
      } else {
        setFillAllDetail(false);
      }
    } else if (master === "Subject Master") {
      if (inputRef.current.value) {
        setDisabled(true);
        addSubjectMaster({
          subject: {
            subjectName: inputRef.current.value,
            subjectType: subjectType,
          },
          id: id,
          did: did,
        })
          .then((res) => {
            if (res?.data) {
              hideModal(false);
              setDisabled(false);
              allSubjectMasterRefetch();
              inputRef.current.value = "";
            }
          })
          .catch({});
      } else {
        setFillAllDetail(false);
      }
    } else {
    }
  };
  return (
    <Dialog open={departmentModal}>
      <div className={style.DModal}>
        <div className={style.title}>
          <h5>{t("create_new_master")}</h5>
          <img
            src="/images/close-post-icon.svg"
            onClick={() => hideModal(false)}
            alt=""
          />
        </div>
        <div hidden={fillAllDetail} className={style.notFound}>
          {t("dill_details_for_masters")}
        </div>
        <div className={style.item} onClick={() => setClick(!click)}>
          <h6>{t("create_master_header_1")}</h6>
          <div className={style.select}>
            <p>{master}</p>
            <img src="/images/select-icon.svg" alt="" />
          </div>

          {click && (
            <div className={style.tooltip}>
              <Options options={selectOptions} switchOption={switchMaster} />
            </div>
          )}
        </div>
        {master !== "Standard/Grade/Level Master" && (
          <div
            className={style.item}
            onClick={() => setClickSubject((pre) => !pre)}
          >
            <h6>Subject Type</h6>
            <div className={style.select}>
              <p>{subjectType}</p>
              <img src="/images/select-icon.svg" alt="" />
            </div>

            {clickSubject && (
              <div className={style.tooltip}>
                <Options
                  customStyle={{ top: "196px", right: "-51px" }}
                  options={selectOptionSubject}
                  switchOption={switchSubjectType}
                />
              </div>
            )}
          </div>
        )}
        <div className={style.input}>
          <h6>{t("create_master_header_2")}</h6>
          <input
            type="text"
            ref={inputRef}
            placeholder={t("e.g. 6th Class/ FYJC")}
            required
            onChange={(e) => {
              if (e.target.value?.length <= 40) {
                setSubjectName(e.target.value);
                setFillAllDetail(true);
              }
            }}
            maxLength="40"
          />
          <StringLength maxLength={40} valueLength={subjectName} />
        </div>

        <button
          className={style.btn}
          onClick={masterSubmitHandler}
          disabled={disabled}
        >
          <p>{t("create")}</p>
          {disabled && (
            <div>
              <LoaderButton />
            </div>
          )}
        </button>
      </div>
    </Dialog>
  );
}

export default CreateMaster;
